@import "variables";

///  .
/// @access public
/// @param {String} $breakpoint -  
/// @requires $breakpoints
@mixin respond-to($breakpoint) {
  $raw-query: map-get($breakpoints, $breakpoint);

  @if $raw-query {
    $query: if(
        type-of($raw-query) == 'string',
        unquote($raw-query),
        inspect($raw-query)
    );

    @media #{$query} {
      @content;
    }
  } @else {
    @error '    `#{$breakpoint}`. '
       + ', ,       `$breakpoints`.';
  }
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}




