}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.clearfix::after {
  clear: both;
  content: '';
  display: table;
}

.btn {
  width: 100%;
  padding: 0.83em 0 0.83em 0;
  font-size: 1.0625rem;
  line-height: 1.18;
  font-weight: 700;
  text-transform: uppercase;
  border: 2px solid #444444;
  background-color: transparent;
  outline: transparent;
  transition: all 0.3s ease-in-out;
}

.btn:hover {
  background-color: #444444;
  color: #ffffff;
}

.btn:active {
  background-color: #444444;
  color: #7c7c7c;
}

.btn--green {
  background-color: #63d1bb;
  color: #ffffff;
}

.btn-burger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 1.25rem;
  width: 1.3125rem;
  padding: 0;
  margin: 0;
  border: 0;
  font-size: 0;
  line-height: 0;
  background-color: transparent;
  outline: transparent;
  cursor: pointer;
}

.btn-burger span,
.btn-burger span::before,
.btn-burger span::after {
  display: block;
  width: 1.3125rem;
  height: 0.1875rem;
  background-color: black;
  transition: all 300ms ease-in-out;
}

.btn-burger span::before,
.btn-burger span::after {
  content: " ";
  position: absolute;
}

.btn-burger span::before {
  top: 0.15rem;
}

.btn-burger span::after {
  bottom: 0.15rem;
}

.btn-burger--opened span {
  background-color: transparent;
}

.btn-burger--opened span:before {
  top: 0.525rem;
  transform: rotate(45deg);
}

.btn-burger--opened span::after {
  bottom: 0.525rem;
  transform: rotate(-45deg);
}

