}

@media (max-width: 767px) {
  .js .navigation .menu-list,
  .js .navigation .user-list {
    position: absolute;
    top: -100rem;
    transform: scale(0);
  }
  .js .navigation--opened .menu-list,
  .js .navigation--opened .user-list {
    position: static;
    transform: scale(1);
