.wrapper {
  width: 20rem;
  margin: 0 auto;

  @include respond-to('medium'){
    width: 48rem;
  }

  @include respond-to('large'){
    width: 78.125rem;
  }
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding:0 !important;
  border:0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.clearfix::after {
  clear: both;
  content: '';
  display: table;
}
